/* services section */



.services {
    background-color: #F5F5F5;
    padding-top: 100px;
    padding-bottom: 40px;
    &__wrapper {
        display: flex;
        flex-direction: column;
        width: 100%;
    }
}
.services__row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.services__item {
    width: 25%;
    display: flex;
    flex-direction: column;
    padding-bottom: 60px;
    &:hover {
        opacity: .7;
        transition: .2s;
        cursor: pointer;
    }
    &:hover .icon {
        color: #10c9c3;
    }
}


i {
    font-size: 48px;
    font-family: "Pe";
    color: rgb(101, 101, 101);
    line-height: 0.625;
    margin: 0 auto;
}
.services__item--name {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 30px;
    color: #191919;
    text-transform: uppercase;
    text-align: center;
}
.services__item--description {
    font-family: 'Nunito Sans', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: #787878;
    text-align: center;
    line-height: 24px;
}

@media (max-width: 768px ) {
    .services {
        &__wrapper {
            flex-direction: row;
            justify-content: space-around;
        }
        &__row {
            flex-direction: column;
        }
        &__item {
            width: 100%;
        }
    }
}
