/* services section */
.services {
  background-color: #F5F5F5;
  padding-top: 100px;
  padding-bottom: 40px;
}

.services__wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.services__row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.services__item {
  width: 25%;
  display: flex;
  flex-direction: column;
  padding-bottom: 60px;
}

.services__item:hover {
  opacity: .7;
  transition: .2s;
  cursor: pointer;
}

.services__item:hover .icon {
  color: #10c9c3;
}

i {
  font-size: 48px;
  font-family: "Pe";
  color: #656565;
  line-height: 0.625;
  margin: 0 auto;
}

.services__item--name {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 30px;
  color: #191919;
  text-transform: uppercase;
  text-align: center;
}

.services__item--description {
  font-family: 'Nunito Sans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: #787878;
  text-align: center;
  line-height: 24px;
}

@media (max-width: 768px) {
  .services__wrapper {
    flex-direction: row;
    justify-content: space-around;
  }
  .services__row {
    flex-direction: column;
  }
  .services__item {
    width: 100%;
  }
}
